.skillsBlock{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #affdaf;
}
.skillsContainer{
    flex-direction: column;
    justify-content: flex-start;

}

.title{
    background-color: lightcyan;
}

.skills{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

}