.project {
    width: 100%;
    max-width: 320px;
    min-height: 400px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 30px 0px;
    cursor: pointer;
    position: relative;
}

.project:hover{
    background-color: black;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0.5;


}

.project a {
    text-decoration: none;
    color: initial;
}


.image {
    height: 320px;
    width: 100%;
    max-width: 320px;
    background-color: oldlace;
    display: flex;
    justify-content: center;
    align-items: center;
    background-size: cover;
    background-position: center;

}

